:root {
  --gray: #8b8484;
  --darkGray: #8e8d8a;
  --main-background: #eae7dc;
  --light-pink: #e97e72;
  --beige: #d8c3a5;
  --light-beige: #e3d9cb;
  --dark-pink: #e85a4f;
}

body {
  background-color: var(--main-background);
  color: var(--darkGray);
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  display: flex;
}

@media (width <= 480px) {
  body, .text {
    font-size: 1rem;
  }
}

nav {
  align-items: right;
  grid-area: nav;
  justify-items: right;
  width: 100%;
  margin-right: 4rem;
  padding: 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

a {
  color: var(--gray);
  text-decoration: none;
  transition: color .2s ease-in-out;
}

a:hover, a:active {
  color: #216f32;
  font-family: Cascadia Code, Consolas, Courier New;
}

ul {
  word-spacing: 1rem;
  flex-direction: column;
  justify-content: right;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.navBar, .startPage, .myWork {
  width: 100%;
}

.wrapDivs {
  background-color: var(--light-beige);
  display: grid;
}

.container {
  flex-direction: column;
  grid-area: main;
  grid-template-areas: "nav nav"
                       "main main"
                       "footer footer";
  place-items: center;
  margin-top: 3rem;
  margin-left: 3rem;
  margin-right: 3rem;
  padding: 20px;
  display: flex;
}

@media (width <= 1024px) {
  .container {
    grid-template-columns: 1fr;
  }
}

.main-content {
  flex-direction: column;
  grid-area: main;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
  display: flex;
}

img {
  object-fit: contain;
  width: 100%;
  height: auto;
}

.profileImg {
  border-radius: 50%;
  max-width: 30%;
  height: auto;
}

p {
  color: var(--gray);
}

.helloWorld, .myWorkText {
  text-align: center;
}

.Nikolina {
  color: var(--dark-pink);
  margin: 0%;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3rem;
}

.Nikolina2 {
  text-align: center;
  color: var(--light-pink);
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3rem;
}

.myWork {
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  display: flex;
}

.portfolioItems {
  border-radius: 1rem;
  width: 20rem;
  height: 20rem;
  margin: 10px;
  transition: box-shadow .3s;
}

.portfolioItems:hover {
  box-shadow: 0 2px 8px #5d5e5e4d;
}

.projectDescriptionDiv {
  background-color: var(--main-background);
  flex-direction: column;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  margin: 1rem;
  display: grid;
}

@media (width <= 768px) {
  .wrapDivs, .projectDescriptionDiv {
    grid-template-columns: 1fr;
  }

  .projectContent {
    flex-direction: column;
  }

  .portfolioItems {
    width: 100%;
  }
}

.projectHeaderDiv {
  background-color: var(--light-pink);
  flex-direction: column;
  align-self: flex-start;
  width: 20rem;
  height: 3rem;
  display: flex;
}

.projectHeaderDiv2 {
  background-color: #e6d251;
  flex-direction: column;
  align-self: flex-start;
  width: 20rem;
  height: 3rem;
  display: flex;
}

.portfolioItems-img {
  width: 100%;
}

.projectHeaderText {
  color: #242423;
  justify-content: left;
  align-items: center;
  margin: auto;
  font-family: Courier New, Courier, monospace;
  font-size: 20px;
  display: flex;
}

.projectDescriptionText {
  margin: 2rem;
}

.projectImgDiv {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  width: 100%;
  display: grid;
}

footer {
  border-top: .5mm solid #303231;
  grid-area: footer;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 3rem;
  padding: 20px;
  display: flex;
}

.footerText {
  color: var(--gray);
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: var(--darkGray);
    color: #fff;
  }
}

@media (prefers-color-scheme: light) {
  body {
    background-color: var(--main-background);
    color: var(--darkGray);
  }
}
